.new-expense__controls
{
    display: flex;
    flex-wrap: wrap;
    gap: 1rem;
    margin-bottom: 1rem;
    text-align: left;
}
.new-expense__control label
{
    font-weight: bold;
    margin-bottom: 0.5rem;
    display: block;
}
.new-expense__control input
{
    font: inherit;
    padding: 0.5rem;
    border-radius: 6px;
    border: 1px solid #ccc;
    width: 20rem;
}
.new-expense__actions
{
    text-align: right;
}
.new-expense__actions button {
    font: inherit;
    cursor: pointer;
    padding: 1rem 2rem;
    border: 1px solid #40005d;
    background-color: #40005d;
    color: #fff;
    border-radius: 12px;
    margin-right: 1rem;
}
.new-expense__actions button:hover, .new-expense__actions button:active
{
    background-color: #510674;
    border-color: #510674;
}
.new-expense__actions button.alternative
{
    color: #220131;
    background-color: transparent;
    border-color: transparent;
}
.new-expense__actions button.alternative:hover, .new-expense__actions button.alternative:active
{
    background-color: #ddb3f8;
}

@media (max-width:450px){
    .new-expense__control input[type="text"]{
        width: 300px;
    }
    .new-expense__control input[type="number"]{
        width: 230px;
    }
    .new-expense__control input[type="date"]{
        width: 180px;
    }
    .new-expense__actions button{
        padding: 10px 15px;
        margin-right: 0;
    }
}